body, div, p {
    font-family: $main-font;
    border:0;
    padding:0;
    margin:0;
}
.global-container {
    width:100%;
    height:100%;
    box-sizing: border-box;
    display:flex;
    justify-content: space-between;
    background-color: $main-back-color;
}
#game-area {
    background-color: $main-back-color;
    flex-grow: 1;
    position:relative;
    overflow:hidden;
}
#ground {
    position:absolute;
    background-color: antiquewhite;
    width:1500px;
    height:1000px;
    top:200px;
    left:300px;
    transition: left 2s ease-in-out, top 2s ease-in-out;
}
#sidebar {
    min-width:250px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding:50px 20px;
    box-sizing: border-box;
    border:5px solid black;
    border-radius: 15px;
    margin: 5px;
    gap:30px;
    .data {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap:25px;
    }
    .indicators {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap:15px;
        font-size: 22px;
        #ammo, #health {
            font-size: 35px;
        }    
    }
}